<script>
import {
  getData,
  getDepartmentListCommon,
  tableResize, //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
export default {
  name: "reportOrderdetrpt",
  data() {
    return {
      authority: {},
      defaultArea: [0], //默认的区域id
      params: {},
      employeeList: {
        options: [],
      }, //业务员
      province: {
        options: [],
      }, //省
      city: {
        options: [],
      }, //市
      area: {
        options: [],
      }, //县
      customer: {
        options: [],
      },
      searchParams: {},
      brandList: {
        options: [],
      }, //品牌
      listShow: false,
      businessTypeList: {
        options: [],
      }, //业务类型列表
      timeValue: [],
      exportType: null,
      companyList: {},
      warehouseList: {
        options: [],
      }, //仓库管理
      TableData: {
        allList: {},
        tableData: [],
      },
      departmentList: {
        options: [],
      },
      // companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      loading: false,
      customerList: {
        options: [],
      }, //客户列表
      per_page: [30, 50, 100, 500],
      tableHeight: 600,
    };
  },

  methods: {
    departmentChange() {
      // this.$set(this.params, "warehouse_id", "");
    },
    getprovinceFocus() {
      this.$common.options("getAreaListOption", this.province, {
        level: 2,
      });
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error",
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportOrderListUrl(s).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    changeDefaultArea(addressId) {
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = "页小计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 12) {
            sums[index] = parseFloat(sums[index] || 0)
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (
            index === 8 ||
            index === 9 ||
            index === 11 ||
            index === 10
          ) {
            sums[index] = parseFloat(sums[index] || 0);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    // exportOrder(scheduleType) {
    //   // 导出库存收发明细表导出URL
    //   if (this.params.export_type == undefined) {
    //     this.$message({
    //       message: "请选择导出格式",
    //       type: "error"
    //     });
    //     return false;
    //   }
    //   if (this.listShow == false) {
    //     this.$message({
    //       message: "请查询需要导出的数据",
    //       type: "error"
    //     });
    //     return false;
    //   }
    //   this.$axMethod.exportOrderListUrl(this.params).then(response => {
    //     if (response.data.code == 200) {
    //       window.location.href = response.data.data.url;
    //     }
    //   });
    // },
    changeProvince() {
      //   this.params.city_id = "";
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        p_id: this.params.province_id,
      });
      this.$set(this.params, "city_id", " ");
      this.$set(this.params, "area_id", " ");
    },
    changeCity() {
      this.$common.options("getAreaListOption", this.area, {
        level: 4,
        p_id: this.params.city_id,
      });
    },
    companyChange(val) {
      // 获取部门
      this.$set(this.params, "warehouse_id", " ");
      getDepartmentListCommon(this, this.params, 1);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.defaultArea = [0];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getOrderList", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      //查询
      this.listShow = true;
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });

      getData(this.searchParams, 2, tableResize.bind(this));
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
  },
  components: {
    getAreaList, //区域列表
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function () {
      elTable2.scrollLeft = this.scrollLeft;
    });
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOrderList", //接口名
      vm: this, //传递的this
    });
    // getData(this.searchParams, 2);
    this.$common.options("getAreaListOption", this.province, {
      level: 2,
    }); //请选择省

    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  },
};
